@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap);
html, body {
  height: 80vh;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  color: #eee;
  background-color: #393E46;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.App {
  width: 400px;
  margin: auto;
  padding: 1.5em 0;
  border: 3px solid rgba(50, 224, 196, 0.5);
  background-color: #222831;
  border-radius: 15px;
}
.Calculator {
  text-align: center;
  margin: auto;
}

.Calculator > h1 {
  font-weight: 700;
  color: #32e0c4;
  margin-top: 0;
}

.Calculator > p {
  font-weight: 500;
  margin: 1em 0 0 0;
}

.Calculator .display {
  font-weight: 300;
}

.Calculator > input {
  text-align: center;
}
